/* index.css (partial) */

/* Modern Font */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

/* Global resets or custom styles can go here */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Base variables for shared properties */
:root {
  --transition-speed: 0.3s;
  --border-radius: 12px;
  --font-main: 'Poppins', sans-serif;
}

/* Light Theme: "Minimal Monochromatic" */
.light-theme {
  /* Primary palette - blue */
  --primary-color: #4361ee;
  --primary-light: #6f86ff;
  --primary-dark: #2f45b5;
  
  /* Monochromatic backgrounds */
  --bg-color: #f8f9fa;
  --surface-color: #ffffff;
  --surface-hover: #f2f2f2;
  
  /* Text colors */
  --text-primary: #2c3e50;
  --text-secondary: #546e7a;
  --text-on-primary: #ffffff;
  
  /* Border & shadow */
  --border-color: rgba(0, 0, 0, 0.08);
  --shadow-color: rgba(0, 0, 0, 0.06);
  
  /* Alias for backwards compatibility - to be used in refactoring */
  --secondary-color: var(--primary-light);
  --accent-color: var(--primary-dark);
  --text-color: var(--text-primary);
  --nav-bg-color: var(--surface-color);
  --nav-text-color: var(--text-primary);
  --card-bg-color: var(--surface-color);
  --card-border-color: var(--border-color);
  --card-hover-shadow: var(--shadow-color);
  --card-selected-border: var(--primary-color);
  --project-item-bg: var(--surface-color);
  --project-item-hover: var(--surface-hover);
  --project-details-bg: var(--surface-color);
}

.light-theme a {
  color: var(--secondary-color); /* #3B82F6 */
  text-decoration: none;
}

.light-theme a:hover {
  color: var(--accent-color);    /* #34D399 */
  text-decoration: underline;    /* optional hover effect */
}

.light-theme .navbar a.active {
  color: var(--accent-color);
  font-weight: bold;
  text-decoration: underline;
}

/* Dark Theme: "Refined Dark" */
.dark-theme {
  /* Primary palette - blue */
  --primary-color: #4361ee;
  --primary-light: #738aff;
  --primary-dark: #364fc0;
  
  /* Monochromatic backgrounds */
  --bg-color: #121212;
  --surface-color: #1e1e1e;
  --surface-hover: #2d2d2d;
  
  /* Text colors */
  --text-primary: #e9ecef;
  --text-secondary: #b0bec5;
  --text-on-primary: #ffffff;
  
  /* Border & shadow */
  --border-color: rgba(255, 255, 255, 0.08);
  --shadow-color: rgba(0, 0, 0, 0.2);
  
  /* Alias for backwards compatibility - to be used in refactoring */
  --secondary-color: var(--primary-light);
  --accent-color: var(--primary-dark);
  --text-color: var(--text-primary);
  --nav-bg-color: var(--surface-color);
  --nav-text-color: var(--text-primary);
  --card-bg-color: var(--surface-color);
  --card-border-color: var(--border-color);
  --card-hover-shadow: var(--shadow-color);
  --card-selected-border: var(--primary-color);
  --project-item-bg: var(--surface-color);
  --project-item-hover: var(--surface-hover);
  --project-details-bg: var(--surface-color);
}

.dark-theme a {
  color: var(--secondary-color); /* #61DAFB */
  text-decoration: none;
}

.dark-theme a:hover {
  color: var(--accent-color);    /* #E06C75 */
  text-decoration: underline;    /* optional hover effect */
}

.dark-theme .navbar a.active {
  color: var(--accent-color);
  font-weight: bold;
  text-decoration: underline;
}

/* Now style the app container and child elements using these vars */
.app-container {
  display: flex;
  min-height: 100vh;
  font-family: var(--font-main);

  /* Apply background and text color from CSS variables */
  background-color: var(--bg-color);
  color: var(--text-color);
  transition: background-color var(--transition-speed) ease,
              color var(--transition-speed) ease;
}

/* Improved navbar with subtle glassmorphism effect */
.navbar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 280px;
  background-color: var(--nav-bg-color);
  padding: 2rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  height: 100vh;
  backdrop-filter: blur(10px);
}

.navbar h1 {
  color: var(--primary-color);
  font-size: 1.8rem;
  margin-bottom: 2rem;
  font-weight: 600;
  position: relative;
}

.navbar h1::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 40px;
  height: 3px;
  background-color: var(--text-color);
  border-radius: 3px;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.navbar li {
  margin: 1rem 0;
}

.navbar a {
  color: var(--nav-text-color);
  text-decoration: none;
  font-size: 1rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.navbar a:hover, .navbar a.active {
  background-color: var(--primary-color);
  color: #ffffff;
  transform: translateX(8px);
}

.theme-toggle {
  padding: 0.75rem 1.5rem;
  background-color: var(--surface-color);
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.3s ease;
  font-weight: 500;
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.theme-toggle:hover {
  background-color: var(--primary-color);
  color: var(--text-on-primary);
}

/* Content container with more padding and max-width */
.content-container {
  flex: 1;
  padding: 2rem 3rem;
  overflow-y: auto;
  max-width: 1200px;
  margin: 0 auto;
}

/* Projects Page Styles */
.projects-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.projects-page h2 {
  margin-bottom: 1.5rem;
  text-align: center;
}

/* Category Grid */
.category-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 2rem;
}

.category-box {
  background-color: var(--card-bg-color);
  border: 2px solid var(--card-border-color);
  border-radius: 8px;
  padding: 1.5rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 120px;
}

.category-box:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px -3px var(--card-hover-shadow);
}

.category-box.selected {
  border-color: var(--card-selected-border);
  box-shadow: 0 0 0 2px var(--card-selected-border);
}

.category-box h3 {
  font-size: 1.1rem;
  margin: 0;
}

/* Project List */
.project-list-container {
  margin-top: 2rem;
}

.project-list-container h3 {
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid var(--card-border-color);
}

.project-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.project-item {
  border-radius: 6px;
  overflow: hidden;
  background-color: var(--project-item-bg);
  transition: all 0.2s ease;
}

.project-header {
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.project-header:hover {
  background-color: var(--project-item-hover);
}

.expand-icon {
  margin-right: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
}

.project-header h4 {
  margin: 0;
  font-size: 1rem;
  font-weight: 500;
}

.project-details {
  padding: 1rem;
  background-color: var(--project-details-bg);
  border-top: 1px solid var(--card-border-color);
}

.project-details p {
  margin: 0;
  line-height: 1.5;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .category-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }
}

@media (max-width: 768px) {
  .category-grid {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(8, 1fr);
  }
}

/* Original project grid styles */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.project-box {
  background-color: var(--card-bg-color);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 5px 15px var(--card-border-color);
  transition: all 0.3s ease;
  position: relative;
  border: 1px solid var(--card-border-color);
}

.project-box:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 30px var(--card-hover-shadow);
}

.project-box img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.project-box .project-content {
  padding: 1.5rem;
}

.project-box h3 {
  font-size: 1.3rem;
  margin-bottom: 0.75rem;
  color: var(--primary-color);
}

.project-box p {
  margin-bottom: 1.25rem;
  line-height: 1.6;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.25rem;
}

.project-tag {
  padding: 0.35rem 0.75rem;
  background-color: var(--primary-light);
  color: var(--text-on-primary);
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 500;
}

.learn-more-link {
  display: inline-block;
  padding: 0.6rem 1.5rem;
  background-color: var(--primary-color);
  color: var(--text-on-primary);
  border-radius: 6px;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
}

.learn-more-link:hover {
  background-color: var(--primary-dark);
  transform: translateX(5px);
  text-decoration: none;
}

/* Responsive media queries */
@media (max-width: 1024px) {
  .about-section {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .about-image {
    max-width: 350px;
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
  }
  
  .navbar {
    width: 100%;
    height: auto;
    padding: 1rem;
    position: relative;
  }
  
  .content-container {
    padding: 1.5rem;
  }
}

/* About section improvements */
.about-section {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  align-items: center;
  padding: 2rem 0;
}

.about-image {
  position: relative;
  width: 100%;
}

.about-image img {
  width: 100%;
  height: auto;
  border-radius: var(--border-radius);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.about-content h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  color: var(--primary-color);
  position: relative;
}

.about-content h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: var(--text-color);
  border-radius: 3px;
}

.about-content p {
  margin-bottom: 1.5rem;
  line-height: 1.8;
  font-size: 1.05rem;
}

/* Demo Apps Page Styles */
.demo-apps-container {
  display: flex;
  height: calc(100vh - 2rem);
  max-height: calc(100vh - 2rem);
  overflow: hidden;
}

.demo-sidebar {
  width: 280px;
  background-color: var(--card-bg-color);
  border-right: 1px solid var(--card-border-color);
  padding: 1rem;
  overflow-y: auto;
}

.demo-sidebar h3 {
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid var(--card-border-color);
}

.demo-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.demo-item {
  padding: 0.75rem 1rem;
  margin-bottom: 0.5rem;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
}

.demo-item:hover {
  background-color: var(--project-item-hover);
}

.demo-item.active {
  background-color: var(--project-item-bg);
  font-weight: 500;
  border-left: 3px solid var(--secondary-color);
}

.demo-icon {
  margin-right: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--secondary-color);
}

.demo-icon.large {
  font-size: 1.5rem;
  margin-right: 1rem;
}

.demo-title {
  flex: 1;
}

.demo-content {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
}

.demo-details h2 {
  margin-bottom: 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid var(--card-border-color);
  display: flex;
  align-items: center;
}

.demo-section {
  margin-bottom: 2rem;
  background-color: var(--card-bg-color);
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.demo-section h3 {
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  color: var(--text-primary);
}

.demo-section h3::before {
  content: none;
  display: none;
}

.demo-section p {
  line-height: 1.6;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-pill {
  background-color: var(--primary-light);
  color: var(--text-on-primary);
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.85rem;
  display: inline-block;
}

.dark-theme .tech-pill {
  background-color: var(--secondary-color);
  color: var(--primary-color);
}

.demo-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  opacity: 0.7;
}

.demo-placeholder h2 {
  margin-bottom: 1rem;
}

/* Responsive adjustments for demo apps */
@media (max-width: 768px) {
  .demo-apps-container {
    flex-direction: column;
    height: auto;
    max-height: none;
  }
  
  .demo-sidebar {
    width: 100%;
    border-right: none;
    border-bottom: 1px solid var(--card-border-color);
  }
  
  .tech-stack {
    justify-content: center;
  }
}

/* Chat Box Styles - Updated to match menu colors */
.chat-box-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
}

.chat-box-toggle {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--primary-color);
  color: var(--text-on-primary);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.chat-box-toggle:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.25);
  background-color: var(--primary-dark);
}

.chat-box {
  position: absolute;
  bottom: 80px;
  right: 0;
  width: 350px;
  background-color: var(--card-bg-color);
  border-radius: 10px;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  transition: all 0.3s ease;
  max-height: 500px;
  display: flex;
  flex-direction: column;
}

.chat-box-header {
  background-color: var(--primary-color);
  color: var(--text-on-primary);
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dark-theme .chat-box-header {
  background-color: var(--primary-color);
  color: var(--text-on-primary);
}

.chat-box-header h3 {
  margin: 0;
  font-size: 1.1rem;
}

.close-button {
  background: none;
  border: none;
  color: var(--text-on-primary);
  font-size: 1.2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.dark-theme .close-button {
  color: var(--text-on-primary);
}

.chat-box-content {
  padding: 20px;
  overflow-y: auto;
  flex-grow: 1;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: 500;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid var(--card-border-color);
  border-radius: 4px;
  background-color: var(--bg-color);
  color: var(--text-color);
  font-family: inherit;
}

.form-group input.error,
.form-group textarea.error {
  border-color: #e74c3c;
}

.error-message {
  color: #e74c3c;
  font-size: 0.85rem;
  margin-top: 5px;
  display: block;
}

.submit-button {
  background-color: var(--primary-color);
  color: var(--text-on-primary);
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  transition: all 0.2s ease;
}

.submit-button:hover {
  background-color: var(--primary-dark);
  opacity: 0.9;
  transform: translateY(-2px);
}

.dark-theme .submit-button {
  background-color: var(--primary-color);
  color: var(--text-on-primary);
}

.submit-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.send-icon {
  margin-left: 8px;
}

.success-message {
  text-align: center;
  padding: 20px 0;
}

.success-message p {
  margin-bottom: 20px;
}

.new-message-button {
  background-color: var(--primary-color);
  color: var(--text-on-primary);
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
}

.new-message-button:hover {
  background-color: var(--primary-dark);
}

.dark-theme .new-message-button {
  background-color: var(--primary-color);
  color: var(--text-on-primary);
}

.error-alert {
  background-color: #fdecea;
  color: #e74c3c;
  padding: 10px;
  border-radius: 4px;
  margin-top: 15px;
  font-size: 0.9rem;
  text-align: center;
}

.dark-theme .error-alert {
  background-color: rgba(231, 76, 60, 0.2);
}

/* Responsive adjustments for chat box */
@media (max-width: 480px) {
  .chat-box {
    width: 300px;
    right: 0;
  }
  
  .chat-box-toggle {
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
}

